import {Directive, Input, ContentChildren, QueryList, ElementRef} from '@angular/core';
import {NgxCardNumberTemplate, NgxCardNameTemplate, NgxCardExpiryTemplate, NgxCardCvcTemplate} from './inputs';

declare var Card;

const defaultPlaceholders = {
	number: '   ',
	name: 'Full Name',
	expiry: '/',
	cvc: '',
};

const defaultMessages = {
	validDate: 'valid\nthru',
	monthYear: 'month/year',
};


export class NgxCard {

	// a selector or DOM element for the container
	// where you want the card to appear
	 container: any;

	 width: number; // optional  default 350px

	// Strings for translation - optional
	_messages;
	 set messages(_messages: any) {
		this._messages = Object.assign({}, defaultMessages, _messages);
	}
	get messages() {
		return this._messages;
	}

	// Default placeholders for rendered fields - optional
	_placeholders;
	 set placeholders(_placeholders: any) {
		this._placeholders = Object.assign({}, defaultPlaceholders, _placeholders);
	}
	get placeholders() {
		return this._placeholders;
	}

	 masks: any;

	 formatting: boolean = true; // optional - default true

	// if true, will log helpful messages for setting up Card
	 debug: boolean = false; // optional - default false

	 numbers: QueryList<NgxCardNumberTemplate>;
	 names: QueryList<NgxCardNameTemplate>;
	 expiries: QueryList<NgxCardExpiryTemplate>;
	 cvcs: QueryList<NgxCardCvcTemplate>;

	constructor(private element: ElementRef) {}

	ngAfterViewInit() {
		new Card({
			form: this.element.nativeElement,
			container: this.container,
			width: this.width,
			formSelectors: {
				numberInput: this.findSelectors(this.numbers),
				expiryInput: this.findSelectors(this.expiries),
				cvcInput: this.findSelectors(this.cvcs),
				nameInput: this.findSelectors(this.names),
			},
			formatting: this.formatting,
			messages: this.messages,
			placeholders: this.placeholders,
			masks: this.masks,
			debug: this.debug,
		});
	}

	findSelectors(list: QueryList<any>): string {
		return list.map(template => template.elementRef.nativeElement.tagName.toLowerCase() + '[name="' + template.name + '"]')
				   .join(', ');
	}

static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: '[card]',
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'container': [{ type: Input },],
'width': [{ type: Input, args: ['card-width', ] },],
'messages': [{ type: Input },],
'placeholders': [{ type: Input },],
'masks': [{ type: Input },],
'formatting': [{ type: Input },],
'debug': [{ type: Input },],
'numbers': [{ type: ContentChildren, args: [NgxCardNumberTemplate, {descendants: true}, ] },],
'names': [{ type: ContentChildren, args: [NgxCardNameTemplate, {descendants: true}, ] },],
'expiries': [{ type: ContentChildren, args: [NgxCardExpiryTemplate, {descendants: true}, ] },],
'cvcs': [{ type: ContentChildren, args: [NgxCardCvcTemplate, {descendants: true}, ] },],
};
};

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
